import {useState} from "react"

export default function NewRecipePage() {

  const [content, setContent] = useState('');
  const [recipes, setRecipes] = useState('');

  const recipesAPI = require('../../utilities/recipes-api')

  function handleSubmit(e) {
    e.preventDefault();
    handleAddRecipe(content)
  }

  async function handleAddRecipe(content){
    const recipe = await recipesAPI.add(content)
    setRecipes([...recipes, recipe])
  }



  return (
    <main>
      <form onSubmit={handleSubmit}>
        <label>Title:</label>
        <input type="text" value={content.title} onChange={e => setContent(e.target.value)}></input>
        <label>Prep. Time:</label>
        <input type="text" value={content.prepTime} onChange={e => setContent(e.target.value)}></input>
        <label>Instructions:</label>
        <textarea id="instructions" type="text" value={content.instructions} onChange={e => setContent(e.target.value)}></textarea>
        <label>Ingredients:</label>
        <input type="text" value={content.ingredients} onChange={e => setContent(e.target.value)} placeholder="Example: Milk, Eggs"></input>
        <button type="reset">Clear</button>
        <button type="submit">Add Recipe</button>
      </form>
    </main>
  );
}